@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: black;
}


.home{display:flex;justify-content:center;max-width:1440px;height:100vh;margin:0 auto;align-items:center;position:relative}.directory-menu{display:grid;grid-template-areas:"men women shoes accessories"  "men  women activewear brands";grid-template-rows:300px 300px;grid-template-columns:300px 300px 320px 320px;grid-gap:15px}.menu-item{cursor:pointer;font-family:"Roboto", sans-serif;min-width:30%;width:100%;border-radius:3.5px;display:flex;padding:30px;align-items:flex-end;background-size:cover;background-position:center;-webkit-filter:saturate(0.7);filter:saturate(0.7)}.menu-item:first-of-type{grid-area:men;background-image:url(/Makintomo04/ecommerce-app/static/media/men3.a9dea04a.jpg);background-size:175%;background-position:-180px -130px}.menu-item:nth-of-type(2){grid-area:women;background-image:url(/Makintomo04/ecommerce-app/static/media/women2.69141f75.jpg)}.menu-item:nth-of-type(3){grid-area:shoes;background-image:url(/Makintomo04/ecommerce-app/static/media/creps.2af0247c.jpg);background-position:-220px 320px;background-size:200%}.menu-item:nth-of-type(4){grid-area:accessories;background-image:url(/Makintomo04/ecommerce-app/static/media/accessory.fac7c75c.jpg);background-position:top}.menu-item:nth-of-type(5){grid-area:activewear;background-image:url(/Makintomo04/ecommerce-app/static/media/brand2.0a95648d.jpg);background-position:left;background-size:140%}.menu-item:nth-of-type(6){grid-area:brands;background-image:url(/Makintomo04/ecommerce-app/static/media/nike.52712c27.jpg)}.menu-item .content{color:white}@media (max-width: 1440px){.directory-menu{grid-template-areas:"men women shoes"  "men  women accessories"  "activewear activewear brands";grid-template-rows:320px 320px 320px;grid-template-columns:400px 400px 480px auto;grid-gap:15px}}

